@font-face {
  font-weight: 400;
  font-family: "Subway-SixInch";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/subwaysix-inch.woff2") format("woff2"), url("../fonts/subwaysix-inch.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "Subway-Footlong";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/subwayfootlong.woff2") format("woff2"), url("../fonts/subwayfootlong.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "Subway-Lettering";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/subwaylettering.woff2") format("woff2"), url("../fonts/subwaylettering.woff") format("woff"); }

body {
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent; }

@-moz-document url-prefix() {
  body {
    font-weight: 200;
    -moz-osx-font-smoothing: grayscale; } }

main {
  display: block; }

input::-ms-clear {
  display: none; }

html {
  height: 100%;
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  height: 100%;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #4a4a4a;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.sr-only {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  margin: -1px; }

.sticky {
  height: 100%;
  display: grid;
  grid-template-rows: auto 1fr; }
